:root {
  --primary: #11998e;
  --secondary: #38ef7d;
  --white: #fff;
  --gray: #9b9b9b;
}

body {
  padding: 0 15px;
}

.search-form {
  width: 400px;
  background-color: rgba(197, 195, 195, .325);
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin: 30px auto 0;
  padding: 30px;
  display: flex;
}

button {
  color: #f0f8ff;
  background-color: #359038;
  border: none;
  border-radius: 40px;
  padding: 14px 40px;
  font-size: 16px;
  font-weight: 400;
  transition: all .5s;
  box-shadow: 0 5px 15px rgba(11, 99, 246, .7);
}

button:hover {
  background-color: #43b247;
  transform: translate(-1%, -5%);
  box-shadow: 0 20px 40px rgba(11, 99, 246, .3);
}

.load-more {
  margin-top: 30px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.load-more:hover, .load-more:focus {
  transform: translate(-51%, -5%);
}

.form__field {
  width: 100%;
  border: 0;
  border-bottom: 2px solid var(--gray);
  color: #000;
  text-align: center;
  background: none;
  outline: 0;
  padding: 7px 0;
  font-family: inherit;
  font-size: 1.3rem;
  transition: border-color .2s;
}

.form__field::-ms-input-placeholder {
  color: rgba(0, 0, 0, 0);
}

.form__field::placeholder {
  color: rgba(0, 0, 0, 0);
}

.form__label {
  color: var(--gray);
  font-size: 1rem;
  transition: all .2s;
  display: block;
  position: absolute;
  top: 0;
}

.form__field:focus {
  border-image: linear-gradient(to right, var(--primary), var(--secondary) );
  border-width: 3px;
  border-image-slice: 1;
  padding-bottom: 6px;
  font-weight: 700;
}

.form__field:focus + .form__label {
  color: var(--primary);
  font-weight: 700;
}

.gallery {
  width: 100%;
  flex-wrap: wrap;
  gap: 30px;
  margin: 100px auto 0;
  display: flex;
}

.photo-card {
  width: calc(25% - 24.5px);
  border-radius: 5px;
  flex-direction: column;
  display: flex;
  box-shadow: 3px 3px 3px rgba(197, 195, 195, .325);
}

.photo-card-image {
  width: 100% align-self: center;
  margin-top: auto;
}

.info {
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: auto;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

@media screen and (max-width: 768px) {
  .photo-card {
    width: calc(50% - 17px);
  }
}

@media screen and (max-width: 375px) {
  .photo-card {
    width: calc(100% - 2px);
  }
}

